import { businessGetAccountInfo, getDepositDetail } from "@/apis/crypto";
import QRCode from "qrcode";

export const cryptoMixins = {
  data() {
    return {
      cryptoAccountInfo: {
        address: "",
        otcTicker: "",
      },
    };
  },
  methods: {
    // 查询币种信息
    async businessGetAccountInfo(val) {
      const { ok, data, msg, ...rest } = await businessGetAccountInfo({ type: val });
      if (ok) {
        this.cryptoAccountInfo = data;
        this.updateQRcode();
      } else {
        this.$toast(msg);
      }

      return { ok, data, msg, ...rest };
    },
    // 更新二维码
    updateQRcode(str) {
      QRCode.toDataURL(str || this.cryptoAccountInfo.address).then((url) => {
        const $QRcode = document.querySelector(".QRcode");
        $QRcode && $QRcode.setAttribute("src", url);
      });
    },
  },
};

export const cryptoMerchantMixins = {
  computed: {
    isCryptoMerchant() {
      return this.$route.query.m === "1";
    },
    amICryptoMerchant() {
      return this.$root.userInfo.BusinessStatus === 1;
    },
    ApiPath() {
      return this.isCryptoMerchant ? "Business" : "";
    },
    BuyerApiPath() {
      // 一个商家单，普通用户买调用普通的，商家买调用商家的
      // 一个普通单，都调用普通的
      // 一个商家单，商家卖调用商家的，普通用户没有商家卖单
      return this.isCryptoMerchant && this.amICryptoMerchant ? "Business" : "";
    },
  },
};

export const cryptoTradingMixins = {
  data() {
    return {
      countdown: 0,
      orderType: "TronDeposit",
      form: {
        paymentType: "",
        amount: "",
      },
      orderInfo: {
        userId: null,
        paymentType: "",
        amount: "",
        passageType: "",
        receivingAddress: "",
        copeAmount: 0,
        createDate: "",
        status: "",
        createTime: "",
        updateTime: "",
        orderId: 0,
        orderNo: "",
      },
    };
  },
  computed: {
    isTrading() {
      return !!this.orderInfo.orderId;
    },
  },
  created() {
    this.getDepositDetail(this.isBuyVb); //isBuyVb 区分区块链交易 取值amount
  },
  methods: {
    // 检查存续订单
    async getDepositDetail(isBuyVb = false) {
      //是否购买V币  true amount为amount  否则 copeAmount
      const { ok, data, msg } = await getDepositDetail({ orderType: this.orderType });
      if (ok) {
        if (data) {
          // 有未完成的订单
          this.$set(this, "orderInfo", data.order);
          this.form.amount = isBuyVb ? data.order.amount : data.order.copeAmount;
          this.form.paymentType = data.order.paymentType;
          this.cryptoAccountInfo.address = data.order.receivingAddress;
          this.cryptoAccountInfo.otcTicker = parseFloat(
            String(data.order.amount / data.order.copeAmount)
          ).toFixed(2);
          this.updateQRcode();
          // 订单超时
          if (data.currentStateExpireTime) {
            if (data.currentStateExpireTime.expire) {
              this.countdown =
                data.currentStateExpireTime.expire * 1000 - this.$root.getServerTime();
              if (this.countdown > 0) {
                const timer = setTimeout(() => this.getDepositDetail(isBuyVb), 5000);
                this.$once("hook:beforeDestroy", () => clearTimeout(timer));
              }
            } else {
              this.countdown = 0;
            }
          }
        } else {
          // 没有未完成的订单
        }
      } else {
        this.$toast(msg);
      }
    },
  },
};

export const cryptoPaymentsMixins = {
  computed: {
    isMerchant() {
      return this.$route.query.m === "1" || this.$route.params.type === "merchant";
    },
    hideAlipayWechat() {
      return this.isMerchant && !this.$root.isMerchantAlipayWechatOpen;
    },
    hideAlipayWechatAtMerchantPage() {
      return !this.$root.isMerchantAlipayWechatOpen;
    },
    payments() {
      return this.isMerchant ? this.$root.merchantPayments : this.$root.payments;
    },
  },
};
