<template>
  <div class="page page-crypto-order-detail">
    <van-cell title="订单编号">
      <van-field :value="orderInfo.orderNo" readonly />
      <button
        v-clipboard="orderInfo.orderNo"
        class="copy copyBtn"
        @success="$root.copySuccessful"
        slot="right-icon"
      />
    </van-cell>

    <van-steps :active="step - 1">
      <van-step>
        {{ new Date() | dateFormat("M/D") }}
        <div class="bottom-status time">
          {{ new Date() | dateFormat("h:m:s") }}
        </div>
      </van-step>
      <van-step>
        区块确认
        <div v-if="step >= 2" class="bottom-status" :class="stepDescClass(stepStatus(2))">
          {{ stepDesc(2) }}
        </div>
      </van-step>
      <van-step>
        货币归集
        <div v-if="step >= 3" class="bottom-status" :class="stepDescClass(stepStatus(3))">
          {{ stepDesc(3) }}
        </div>
      </van-step>
      <van-step>
        交易完成
        <div v-if="step >= 4" class="bottom-status" :class="stepDescClass(stepStatus(4))">
          {{ stepDesc(4) }}
        </div>
      </van-step>
    </van-steps>

    <img v-if="orderInfo.receivingAddress" class="QRcode" alt="qr code" />
    <div class="notice">此地址只可接受{{ orderInfo.paymentType }}</div>

    <van-cell-group>
      <van-cell title="转入地址">
        <van-field :value="orderInfo.receivingAddress" readonly />
        <button
          v-clipboard="orderInfo.receivingAddress"
          class="copy copyBtn"
          @success="$root.copySuccessful"
          slot="right-icon"
        />
      </van-cell>
      <van-cell title="购买V币金额">
        <van-field input-align="right" readonly :value="orderInfo.amount | amountFormat">
          <template #extra>
            <span>V币</span>
          </template>
        </van-field>
      </van-cell>
      <van-cell :title="`应付${orderInfo.paymentType}`">
        <van-field input-align="right" readonly :value="orderInfo.copeAmount | amountFormat">
          <template #extra>
            <span> {{orderInfo.paymentType}}</span>
          </template>
        </van-field>
      </van-cell>
      <van-cell :title="`实付${orderInfo.paymentType}`">
        <van-field input-align="right" readonly :value="orderInfo.actualAmount | amountFormat">
          <template #extra>
            <span> {{orderInfo.paymentType}}</span>
          </template>
        </van-field>
      </van-cell>
      <van-cell title="到账V币金额">
        <van-field input-align="right" readonly :value="orderInfo.depositAmount | amountFormat">
          <template #extra>
            <span>V币</span>
          </template>
        </van-field>
      </van-cell>
    </van-cell-group>

    <van-cell :title="`${orderInfo.paymentType}价格`">
      <van-field input-align="right" readonly :value="orderInfo.feeRate | amountFormat">
        <template #extra>
          <span>￥</span>
        </template>
      </van-field>
    </van-cell>
  </div>
</template>

<script>
import { getOrderDetail } from "@/apis/crypto";
import { cryptoMixins } from "@/vendor/mixins";
export default {
  mixins: [cryptoMixins],
  data() {
    return {
      orderNo: "",
      transinAddr: "",
      coinType: "",
      orderInfo: {
        orderId: 0,
        orderNo: "",
        userId: 0,
        paymentType: "",
        passageType: "",
        amount: "",
        copeAmount: "",
        actualAmount: "",
        receivingAddress: "",
        paymentAddress: "",
        appeal: 0,
        operatorAdminId: 0,
        status: "",
        createDate: "",
        createTime: "",
        finishDate: "",
        finishTime: "",
        updateTime: "",
        txid: "",
        remark: "",
        toTxid: "",
        feeRate: 0,
      },
    };
  },
  created() {
    this.$root.$emit("root:set-topbar", ["left-back", "customer-service"]);
    this.getOrderDetail();
  },
  computed: {
    step() {
      switch (this.orderInfo.status) {
        case "CollectedSuccess":
          return 4;
        case "CollectedFail":
        case "WaitingCollected":
          return 3;
        case "WaitingTransfer":
        case "TransferTimeOut":
        case "AutoCancel":
        case "Manualcancel":
          return 2;
        default:
          return 1;
      }
    },
  },
  methods: {
    stepDescClass(code) {
      if (code === 1) {
        return "success";
      }
      if (code === 0) {
        return "failed";
      }
      if (code === -1) {
        return "normal";
      }
    },
    stepStatus(slot) {
      if (slot === 2 && this.step > 2) {
        return 1;
      }
      if (slot === 3 && this.step > 3) {
        return 1;
      }
      switch (this.orderInfo.status) {
        case "CollectedSuccess":
          return 1;
        case "WaitingTransfer":
        case "WaitingCollected":
          return -1;
        case "CollectedFail":
        case "AutoCancel":
        case "Manualcancel":
        case "TransferTimeOut":
          return 0;
        default:
          break;
      }
    },
    stepDesc(slot) {
      if (slot === 2 && this.step > 2) {
        return "已确认";
      }
      if (slot === 3 && this.step > 3) {
        return "已归集";
      }
      switch (this.orderInfo.status) {
        case "WaitingTransfer":
          return "等待转账";
        case "TransferTimeOut":
          return "转账超时";
        case "WaitingCollected":
          return "等待归集";
        case "CollectedFail":
          return "归集失败";
        case "CollectedSuccess":
          return "订单完成";
        case "AutoCancel":
          return "自动取消";
        case "Manualcancel":
          return "人工取消";
        default:
          break;
      }
    },
    // 获取订单详情
    async getOrderDetail() {
      const { ok, data, msg } = await getOrderDetail({ orderNo: this.$route.params.orderNo });
      if (ok) {
        this.orderInfo = data.order;
        this.updateQRcode(this.orderInfo.receivingAddress);
      } else {
        this.$toast(msg);
      }
    },
  },
};
</script>

<style lang="scss">
