  overflow-y: auto;
  .van-steps {
    &__items {
      margin-bottom: 30px;

      .van-step--horizontal:not(:first-child) {
        .bottom-status {
          left: 50%;
          transform: translateX(-50%);
        }
      }

      .van-step--horizontal:last-child {
        .bottom-status {
          left: initial;
          right: 0;
          transform: none;
        }
      }
    }

    .bottom-status {
      position: absolute;
      top: 44px;
      white-space: nowrap;

      &.success {
        color: #15b300;
      }

      &.failed {
        color: #fb2222;
      }
    }
  }

  .QRcode {
    display: block;
    width: 250px;
    height: 250px;
    font-size: 250px;
    margin: 0 auto;
  }

  .notice {
    font-size: 14px;
    text-align: center;
    padding-top: 10px;
    margin: 10px 0;
  }

  .copyBtn {
    &.copy {
      height: 24px;
      padding: 0 10px;
    }

    margin-left: auto;

    &::after {
      background-color: #999999;
    }

    &::after,
    &::before {
      border-color: #999999;
    }
  }

  .van-cell {
    &__title {
      padding-left: 5px;
    }
    &__value {
      flex: 2;
      .van-field {
        padding-top: 0;
        padding-bottom: 0;
      }
    }
  }
}
</style>
